import React from 'react'
import "./Menu.css"
import { Link } from "react-router-dom";
import { auth } from "../../Firebase/config";
const Menu = () => {
  return (
<div class="sidebar">
      <h3>Menu</h3>
      <ul>
        <Link to="/">
        <i class="fa-solid fa-house"></i>
        Dashboard
        </Link>
        <Link to="/locations">
        <i class="fa-solid fa-location-dot"></i>
        Locations
        </Link>
        <Link to="/supervisors">
        <i class="fa-solid fa-user-group"></i>
         Supervisors
        </Link>
        <Link to="/observers">
        <i class="fa-solid fa-users"></i>
         Observers
        </Link>

        <Link className="log" to="/" onClick={() => auth.signOut()}>
        <i class="fa-solid fa-right-from-bracket"></i>
         Log out
        </Link>
      </ul>
    </div>

  )
}


export default Menu

